.first-screen {
   margin-top: rem(-55);
   @media (max-width: 1000px) {
      margin-top: rem(30);
   }
   min-height: rem(565);
   position: relative;
   overflow: hidden;
   &:before {
      position: absolute;
      bottom: 0;
      left: 0;
      height: 100%;
      width: 100%;
      background: left bottom url(@img/first-screen/oak-leave.svg) no-repeat;
      z-index: -5;
      @media (min-width: 1200px) {
         content: "";
      }
   }
   &:after {
      position: absolute;
      bottom: 0;
      right: 0;
      height: 100%;
      width: 100%;
      background: right bottom url(@img/first-screen/leaves.svg) no-repeat;
      z-index: -5;
      @media (min-width: 1200px) {
         content: "";
      }
   }
   // .first-screen__container
   &__container {
      display: grid;
      grid-template:
         "nav       main" auto
         "features  features" auto / minmax(270px, pc(381, $maxWidthContainer))
         1fr;
      column-gap: rem(32);
      @media (max-width: 650px) {
         grid-template:
            "nav"
            "main"
            "features";
      }
   }
   // .first-screen__nav
   &__nav {
      // flex: 0 0 pc(381, $maxWidthContainer);
      flex: 0 0 rem(381);
      grid-area: nav;
   }
   // .first-screen__menu
   &__menu {
      position: relative;
   }
   // .first-screen__slogan
   &__slogan {
      background: url(@img/first-screen/stamp.svg) no-repeat;
      margin-top: rem(-47);
      width: rem(189);
      height: rem(189);
      display: flex;
      align-items: center;
      justify-content: center;
      margin-left: rem(28);
      position: relative;
      @media (max-width: 1000px) {
         margin-top: rem(20);
      }
      @media (max-width: 650px) {
         display: none;
      }
   }
   &__slogan-text {
      color: #fff;
      font-size: rem(14);
      line-height: math.div(17, 14);
      transform: rotate(-20.16deg);
      text-align: center;
      margin-top: rem(5);
      margin-left: rem(10);
   }
   // .first-screen__main
   &__main {
      grid-area: main;
      max-height: rem(537);
   }
   // .first-screen__features
   &__features {
      grid-area: features;
   }
}
.fs-menu {
   &__paw-wrapper {
      position: relative;
      padding-bottom: 105%;
   }
   &__paw {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      user-select: none;

      path {
         cursor: pointer;
         transition: fill .2s;
         fill: $mainGreen;
      }
      text {
         pointer-events: none;
      }
      @media (any-hover: hover) {
         path {
            &:hover {
               fill: #82dbec;
            }
         }
      }
      &--large {
      }
   }
  
   // .fs-menu__item
   &__item {
      position: absolute;
      text {
         font-weight: 500;
         text-align: center;
         fill: white;
         font-size: rem(16);
      }
      .first-line {
         display: block;
         font-size: rem(20);
         line-height: math.div(24, 20);
         font-weight: 600;
      }
      span {
         display: block;
         font-size: rem(20);
         line-height: math.div(24, 20);
         font-weight: 600;
      }
      &--first {
         top: pc(138, 379);
         left: 0%;
         // transform: rotate(-28.84deg);
         border-radius: rem(70);
         a {
            // padding: rem(44) rem(12);
         }
         text {
            transform: rotate(-28.84 10 212);
         }
      }
      &--second {
         .second-line {
            font-size: rem(9);
            line-height: math.div(11, 9);
         }
         top: pc(23, 379);
         left: pc(64, 381);
         a {
            // transform: rotate(-14.61deg);
            // padding: rem(46) rem(12);
         }
         span {
            margin-bottom: em(7, 20);
         }
      }
      &--third {
         .second-line {
            font-size: rem(12);
            line-height: math.div(12, 10);
         }
         top: 0;
         left: pc(179, 381);
         a {
            // padding: rem(46) rem(22);
         }
         span {
            margin-bottom: em(5, 20);
         }
      }
      &--fourth {
         .first-line {
            font-size: rem(22);
            line-height: math.div(20, 17);
         }
         top: pc(88, 379);
         right: 0;
         a {
            // transform: rotate(24.28deg);
            // padding: 3.575rem 0.775rem;
         }
      }
      &--fith {
         .first-line,
         .second-line {
            font-size: rem(40);
            line-height: math.div(38, 40);
         }
         bottom: 0;
         left: pc(91, 381);
         a {
            transform: rotate(-13.84deg);
            // padding: rem(80) rem(80) rem(61) rem(63);
         }
         span {
            font-size: inherit;
            line-height: inherit;
            font-weight: inherit;
         }
      }
   }
}
.fs-menu__paw--small {
   .first-line {
      font-size: 15px;
      font-weight: 600;
   }

   .fs-menu__item--first {
      .second-line {
         font-size: 12px;
      }
   }
   .fs-menu__item--second {
      .second-line,
      .third-line,
      .fourth-line {
         font-size: 12px;
      }
   }
   .fs-menu__item--third {
      .second-line,
      .third-line {
         font-size: 13px;
      }
   }
   .fs-menu__item--fourth {
      .first-line {
         font-size: 16px;
      }
      .second-line {
         font-size: 10px;
      }
   }
   .fs-menu__item--fith {
      .first-line,
      .second-line {
         font-size: 24px;
         font-weight: 600;
      }
   }
}

.main {
   position: relative;
   &__bg {
      display: none;
      @media (min-width: 800px) {
         display: block;
         position: absolute;
         right: 0;
         margin-right: -10%;
         bottom: 0;
         max-width: 100%;
         z-index: -1;
      }
   }
   // .main__title
   &__title {
      @include avalue("font-size", 48, 25);
      line-height: math.div(59, 48);
      font-weight: 600;
      margin-top: rem(55);
      grid-area: title;
      &:not(:last-child) {
         margin-bottom: rem(20);
      }
      @media (max-width: 1000px) {
         margin-top: rem(50);
         margin-bottom: rem(30);
      }
   }
   &__title-br {
      display: inline;
      @media (max-width: 650px) {
         display: none;
      }
   }
   // .main__subtitle
   &__subtitle {
      font-size: rem(18);
      line-height: math.div(25, 18);
      grid-area: subtitle;
      &:not(:last-child) {
         margin-bottom: rem(24);
      }
      p {
         &:not(:last-child) {
            margin-bottom: rem(15);
         }
      }
      @media (max-width: 650px) {
         font-weight: 500;
      }
   }
   // .main__form
   &__form {
      display: flex;
      flex-direction: column;
      width: rem(230);
      font-weight: 500;
      grid-area: form;
   }
   &__row {
      &:not(:last-child) {
         margin-bottom: rem(18 - 17);
      }
   }
   &__error-list {
      height: rem(17);
      min-height: auto;
   }
   &__button {
      margin-top: rem(30);
      font-size: rem(18);
      line-height: math.div(25, 18);
      font-weight: 400;
   }
}

.features {
   display: flex;
   align-items: center;
   flex-wrap: wrap;
   @include avalue("gap", 70, 20);
   padding: rem(30) 0;
   // .features__feature
   &__feature {
      max-width: rem(320);
      display: flex;
      gap: rem(20);
      &::before {
         font-size: rem(38);
         @include flex-center;
         color: $mainPink;
      }
   }
}
.fs-menu__burger{
   &-item {
      display: block;
      font-size: 24px;
      line-height: 1.2;
      padding: 30px 15px;
      @media (max-width: 375px) {
         font-size: 16px;
         padding: 20px 10px;
      }
   }
}
.burger {
   width: 75px;
   height: 60px;
   z-index: 10;
   display: flex;
   flex-direction: column;
   row-gap: 10px;
   position: fixed;
   right: 0;
   top: 0;
   padding: 15px;
   background-color: $mainPink;
   border-radius: 0 0 0 5px;
   opacity: 1;
   transition: opacity .2s;
   cursor: pointer;
   z-index: 10;
   span, &::before, &::after {
      content: "";
      width: 100%;
      height: 2px;
      background-color: $mainYellow;
      display: block;
   }
   &--closed {
      opacity: 0;
      pointer-events: none;
   }
}
.first-screen {
   .popup {
      display: flex;
      justify-content: flex-end;
      &-container {
         background-color: $mainYellow;
         display: block;
         padding: 20px 10px;
         height: auto;
      }
   }
   .popup-header {
      display: flex; 
      justify-content: flex-end;
      width: 100%;
      margin-top: 10px;
   }
   .popup-close-button {
      width: 30px;
      height: 30px;
      margin-right: 30px;
      position: relative;
      cursor: pointer;
      &::before, &::after {
         content: "";
         width: 30px;
         height: 2px;
         background-color: $mainTextColor;
         display: block;
         transform-origin: center;
         position: absolute;
         top: 0;
         left: 0;
      }
      &::before {
         transform: translateY(50%) rotate(-45deg);
      }
      &::after  {
         transform: translateY(50%) rotate(45deg);
      }
   }
}
